<template>
  <div id="record" :style="{backgroundImage:'url('+bg+')'}">
    <!--用户信息-->
    <user></user>
    <!--记录列表-->
    <mt-navbar v-model="activeTab" class="navTab">
      <mt-tab-item id="1" class="sizeTxt">北京赛车</mt-tab-item>
      <mt-tab-item id="2" class="sizeTxt">幸运飞艇</mt-tab-item>
    </mt-navbar>
    <mt-tab-container v-model="activeTab" class="details">
      <mt-tab-container-item id="1">
        <div class="tabCont">
            <table class="tabHeader">
                <thead>
                    <tr>
                      <th width="12%">期号</th>
                      <th width="45%">出号</th>
                      <th width="18%">冠亚和</th>
                      <th width="25%">1-5名龙虎</th>
                    </tr>
                </thead>
            </table>
            <table class="tabNr">
                <tbody>
                    <tr v-for="(item,index) in airshipData" :key="index">
                      <td width="12%">{{item.itemId}}</td>
                      <td width="45%">
                        <img v-for="(item2,ind) in item.outputSrc" :key="ind"  class="number" :src="item2"/>
                      </td>
                      <td width="18%">
                        <img v-for="(item2,ind) in item.sizeSrc" :key="ind"  class="number" :src="item2"/>
                      </td>
                      <td width="25%">
                        <img v-for="(item2,ind) in item.dragonSrc" :key="ind"  class="number" :src="item2"/>
                      </td>
                    </tr>
                </tbody>
            </table>
        </div>
      </mt-tab-container-item>
      <mt-tab-container-item id="2">
        <div class="tabCont">
          <table class="tabHeader">
            <thead>
            <tr>
              <th width="12%">期号</th>
              <th width="45%">出号</th>
              <th width="18%">冠亚和</th>
              <th width="25%">1-5名龙虎</th>
            </tr>
            </thead>
          </table>
          <table class="tabNr">
            <tbody>
            <tr v-for="(item,index) in airshipData" :key="index">
              <td width="12%">{{item.itemId}}</td>
              <td width="45%">
                <img v-for="(item2,ind) in item.outputSrc" :key="ind"  class="number" :src="item2"/>
              </td>
              <td width="18%">
                <img v-for="(item2,ind) in item.sizeSrc" :key="ind"  class="number" :src="item2"/>
              </td>
              <td width="25%">
                <img v-for="(item2,ind) in item.dragonSrc" :key="ind"  class="number" :src="item2"/>
              </td>
            </tr>
            </tbody>
          </table>
        </div>
      </mt-tab-container-item>
    </mt-tab-container>
    <!-- 底部导航菜单 -->
    <tabs :selected="selected"></tabs>
  </div>
</template>

<script>
import tabs from './../components/tabs'
import user from './../components/user'
export default {
  name: 'record',
  data () {
    return {
      selected: 'record', // 导航当前页的状态
      activeTab: '1', // 默认选中
      airshipData: [], // 北京赛车数据
      menubars: 7,
      bg: require('./../assets/images/game21.png'), // 背景图片

      // 走势图片
      hu: require('./../assets/images/game102.png'), // 虎图片
      long: require('./../assets/images/game103.png'), // 龙图片
      da: require('./../assets/images/game104.png'), // 大图片
      xiao: require('./../assets/images/game105.png'), // 小图片
      dan: require('./../assets/images/game106.png'), // 单图片
      shuang: require('./../assets/images/game107.png'), // 双图片
      ling: require('./../assets/images/game108.png'), // 0图片
      yi: require('./../assets/images/game109.png'), // 1图片
      er: require('./../assets/images/game110.png'), // 2图片
      san: require('./../assets/images/game111.png'), // 3图片
      si: require('./../assets/images/game112.png'), // 4图片
      wu: require('./../assets/images/game113.png'), // 5图片
      liu: require('./../assets/images/game114.png'), // 6图片
      qi: require('./../assets/images/game115.png'), // 7图片
      ba: require('./../assets/images/game116.png'), // 8图片
      jiu: require('./../assets/images/game117.png') // 9图片
    }
  },
  components: {
    tabs, user
  },
  created () {
    // 北京赛车
    let itemId = 730995
    for (let i = 0; i < 40; i++) {
      this.airshipData[i] = {}
      itemId++
      this.airshipData[i].itemId = itemId
      let outputSrc = []
      for (let i = 0; i < 10; i++) {
        switch (Math.floor(Math.random() * 10)) {
          case 0 :
            outputSrc.push(this.ling)
            break
          case 1 :
            outputSrc.push(this.yi)
            break
          case 2 :
            outputSrc.push(this.er)
            break
          case 3 :
            outputSrc.push(this.san)
            break
          case 4 :
            outputSrc.push(this.si)
            break
          case 5 :
            outputSrc.push(this.wu)
            break
          case 6 :
            outputSrc.push(this.liu)
            break
          case 7 :
            outputSrc.push(this.qi)
            break
          case 8 :
            outputSrc.push(this.ba)
            break
          case 9 :
            outputSrc.push(this.jiu)
            break
        }
      }
      this.airshipData[i].outputSrc = outputSrc
      // 大小单双
      let sizeSrc = []
      for (let i = 0; i < 4; i++) {
        if (i === 0) {
          switch (Math.floor(Math.random() * 10)) {
            case 0 :
              sizeSrc.push(this.ling)
              break
            case 1 :
              sizeSrc.push(this.yi)
              break
            case 2 :
              sizeSrc.push(this.er)
              break
            case 3 :
              sizeSrc.push(this.san)
              break
            case 4 :
              sizeSrc.push(this.si)
              break
            case 5 :
              sizeSrc.push(this.wu)
              break
            case 6 :
              sizeSrc.push(this.liu)
              break
            case 7 :
              sizeSrc.push(this.qi)
              break
            case 8 :
              sizeSrc.push(this.ba)
              break
            case 9 :
              sizeSrc.push(this.jiu)
              break
          }
        } else {
          switch (Math.floor(Math.random() * 4)) {
            case 0 :
              sizeSrc.push(this.da)
              break
            case 1 :
              sizeSrc.push(this.xiao)
              break
            case 2 :
              sizeSrc.push(this.dan)
              break
            case 3 :
              sizeSrc.push(this.shuang)
              break
          }
        }
      }
      this.airshipData[i].sizeSrc = sizeSrc
      let dragonSrc = []
      for (let i = 0; i < 5; i++) {
        switch (Math.floor(Math.random() * 4)) {
          case 0 :
            dragonSrc.push(this.long)
            break
          case 1 :
            dragonSrc.push(this.hu)
            break
          case 2 :
            dragonSrc.push(this.long)
            break
          case 3 :
            dragonSrc.push(this.hu)
            break
          case 4 :
            dragonSrc.push(this.hu)
            break
        }
      }
      this.airshipData[i].dragonSrc = dragonSrc
    }
    this.$axios.post('http://www.xv88.cn/Lottery/Controllers/DataDealing.ashx?action=record',{
      count: this.menubars
    }).then((data) => {
      console.log(data)
    })
  }

}
</script>

<style scoped>
  #record{overflow-y: scroll !important; background-size: 100% 100%;height: 100%;position: fixed;width: 100%;}
  /*北京赛车和幸运飞艇切换样式*/
  .navTab{height: 2rem; width: 54%; margin-left: 5.5%; border: 1px solid #f44360; border-bottom: 0; line-height: 2rem;border-bottom: 0.09rem;}
  .tabCont{border: 1px solid #f44360;margin-bottom: 5rem;overflow: auto; margin-left:5.5%; width: 89%;  font-size: 12px;color: #fff; text-align: center;}
  .tabHeader{background: #321311; border-bottom: 1px solid #f44360;border-collapse:collapse; width: 100%; font-size: 12px;color: #fff; text-align: center;}
  .tabHeader tr th{font-size: 0.7rem;padding: 0.2rem 0; border-right:1px solid #f44360; }
  .tabHeader tr th:last-child{border-right: none;}
  .tabNr{border-collapse:collapse; width: 100%;overflow-y: scroll; font-size: 12px;color: #fff; text-align: center;}
  .tabNr tr{border-bottom: 0.05rem solid #7c7c7c;}
  .tabNr tr:last-child{border-bottom: none;}
  .tabNr tr td{ font-size: 0.7rem;}
  .tabNr tr td img{ display: inline-block; width: 0.8rem; padding:0 0.04rem;margin-top: 0.2rem;}
</style>



